import React, { Component } from 'react';
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom';
import axios from 'axios';
import "./styles/app.scss";

import Header from './Components/Header';
import MainMenu from './Components/MainMenu';
import MainResults from './Components/MainResults';
import SubResults from './Components/SubResults';

class App extends Component {
  constructor() {
      super();
      this.state = {
        searchQuery: '',
        results: {people: null, films: null, planets: null},
        isLoading: false,
      };
  };

  userChoice = async (e) => {
    await this.setState({
      searchQuery: e.target.name,
    })

    this.getData(this.state.searchQuery, 1)
  }

  getData = (whatToGet, page) => {
    this.setState({ isLoading: true });
    axios({
      url: `https://swapi.dev/api/${whatToGet}`,
      method: 'GET',
      params: {
        page: page,
    },
  })
  .then( (res) => {
    this.setState({
      isLoading: false,
      results: {
        ...this.state.results,
        [this.state.searchQuery]: res.data,
      }
    })
  })
  .catch( error => { console.log(error); })
  }

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Route exact path="/">
          <Header />
          {/* <MainMenu
            states={this.state} 
            userChoice={this.userChoice}
            gettingData = {this.getData}
          /> */}
            <li><NavLink to="/people" name="people" onClick={ this.userChoice } >People</NavLink></li>
            <li><NavLink to="/films" name="films" onClick={  this.userChoice } >Movies</NavLink></li>
            <li><NavLink to="/planets" name="planets" onClick={ this.userChoice } >Planets</NavLink></li>
        </Route>
        
        <Route exact path={["/people", "/films", "/planets"]}>
          { !this.state.isLoading && this.state.results[this.state.searchQuery] ? <MainResults states={this.state} switchPage={this.getData} /> : <div>Loading...</div>
          }
        </Route>

        <Route path={["/people/:peopleID", "/films/:filmsID", "/planets/:planetsID"]}  component={ SubResults } />
      </Router>
    );
  }
}
export default App;
